import React from "react";
import { Col, Row, Card } from "antd";
import StatusItem from "./StatusItem";
import HistoryViewButton from "./HistoryViewButton";

class GameStatus extends React.Component {
  render = () => {
    const {
      roundIdx,
      wallet,
      resilientLevel,
      cattleInventory,
      productInventory,
      inventoryCapacity,
      processingCapacity,
      numOfRound,
    } = this.props.value;
    return (
      <Card>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem
              title="Current Round"
              value={roundIdx + 1}
              prefix="Round "
              suffix={"/ " + numOfRound}
            />
          </Col>
          <Col span={12}>
            {roundIdx > 0 && (
              <div style={{ marginTop: 36 }}>
                <HistoryViewButton />
              </div>
            )}
          </Col>
        </Row>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem title="Wallet (USD)" value={wallet} prefix="$" />
          </Col>
          <Col span={12}>
            <StatusItem
              title="Resilience"
              value={resilientLevel}
              prefix="Level"
            />
          </Col>
        </Row>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem
              title="Cattle Inventory"
              value={cattleInventory}
              suffix="kg"
            />
          </Col>
          <Col span={12}>
            <StatusItem
              title="Product Inventory"
              value={productInventory}
              suffix="kg"
            />
          </Col>
        </Row>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem
              title="Inventory Capacity"
              value={inventoryCapacity}
              suffix="kg"
            />
          </Col>
          <Col span={12}>
            <StatusItem
              title="Processing Capacity"
              value={processingCapacity}
              suffix="kg"
            />
          </Col>
        </Row>
      </Card>
    );
  };
}

export default GameStatus;
